<template>
  <div>
     污染源
  </div>
</template>

<script>
export default {
  computed:{
   
    stationInfo:{
      get: function () {
        return this.$store.state.stationInfo;
      },
    }
  },
   watch:{
    stationInfo(value) {
      if (value.length > 0) {
       
      
        if(value[0].stationType1 === '001'){
          this.$router.push('/qualityControl/testdata/test1')
        }else if(value[0].stationType1 === '002'){
          console.log(2)
        }
       
      }
    }
  },
}
</script>

<style>
#app {
  height: 100%;
}
</style>



